<template>
  <div class="personal-content-body">
    <div class="table-content">
      <div class="clear table-content-title">
        <div class="left">项目管理</div>
        <div class="flex_end table-content-title-right">
        		<el-checkbox v-model="isOrderByPeople" @change="cutNav(current,floor)">人员排序</el-checkbox>
        		<el-input placeholder="搜索" v-model="keyWord" class="header-right-time" :clearable='true' @change="cutNav(current,floor)">
	          <i slot="prefix" class="el-input__icon el-icon-search "></i>
	        </el-input>
        </div>
        
      </div>
      <div class="table-group">
        <div class="table-group-content">
          <div class="clear table-group-title">
            <div class="left breadcrumb">
              <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item>项目总数（{{total}}）</el-breadcrumb-item>
              </el-breadcrumb>
            </div>
            <div class="right">
              <div class="table-group-title-btn btn-blue" @click="AddPriject">
                <span class="iconfont icon-add"></span>添加新项目
              </div>
            </div>
            <div class="right">
              <label for="upload-project-input">
                <div class="table-group-title-btn btn-blue">
                  <span class="iconfont icon-daochu2"></span>导入项目信息
                </div>
              </label>
              <input type="file" ref="inputer" id="upload-project-input" style="display:none" v-on:change="uploadProjectExcel($event)" />
            </div>
            <div class="right">
              <div class="table-group-title-btn btn-blue" @click="toDownloadCusExcel">
                <span class="iconfont icon-daoru"></span>下载项目信息模版
              </div>
            </div>
            <div class="right">
              <div class="table-group-title-btn btn-blue" @click="downloadProjectInfo">
                <span class="iconfont icon-daoru"></span>导出excel表格
              </div>
            </div>
            <div class="line"></div>
            <div class="navbox">
              <div class="navItem " v-for="(item,index) of naveList" :key="index" :class="{'current':index==current}" @click="cutNav(index,item.floor)">
                <span v-if="item.floor" class="icon">{{item.floor}}</span>
                <span class="name">{{item.name }}</span>
                <span class="number" v-if="item.num!=0&&item.num!=undefined">{{item.num>99?'..':item.num}}</span>
                <span class="triangle"></span>
              </div>
              <!-- <div class="navItem "  :class="{'current':naveList.length==current}" @click="cutNav(naveList.length)">
                      <span class="icon"></span>
                      <span class="name">储备项目库</span>
                      <span class="triangle"></span>
                  </div> -->
            </div>
          </div>
          <!-- pe数据列表 -->
          <div class="historyapply-group-tablebox table-group-tablebox">
            <div class="table-normal-box">
              <table class="table-normal">
                <tr>
                  <th>序号</th>
                  <th>项目名称</th>
                  <th>项目负责人</th>
                  <th>创建时间</th>
                  <th>项目状态</th>
                  <th>操作</th>
                </tr>
                <tr v-for="(item,index) in datalist" :key="item.data_id">
                  <td>{{index+1}}</td>
                  <td>{{item.companyName}}</td>
                  <td>{{item.name}}</td>
                  <td>{{item.add_time}}</td>
                  <td>
                    <em v-if="item.status==-2">待提交</em>
                    <em v-if="item.status==-1">已否决</em>
                    <em v-if="item.status==0" style='color:red'>待审批</em>
                    <em v-if="item.status==1">审批中</em>
                    <em v-if="item.status>=2">同意通过</em>
                    <!-- <em v-if="item.status==2">同意上立项会</em> -->
                  </td>
                  <td>
                    <span class="table-link" @click="toNewProjectProcess(item.company_id,item.companyName)">查看</span>
                    <span class="table-link" @click="goToDelete(item.company_id,1)">删除</span>
                  </td>
                </tr>
              </table>
              <div class="table-nodata" v-if="datalist.length==0">
                <img src="../../images/table_nodata.png" alt=""> 没有对应数据
              </div>
            </div>
            <!-- <div class="pagination-normal" v-if="total!=0">
                <el-pagination background layout="prev, pager, next" :total="total" :page-size="pageSize" prev-text="上一页" next-text="下一页" @current-change="currentChange" :current-page="currentPage">
                </el-pagination>
              </div> -->
          </div>
        </div>
      </div>
      <el-dialog :visible.sync="addaccountDialogVisible1" width="680px" center :append-to-body='true' :before-close="toCancleConfirm1" class="zxMoney_alert  project_alert">
        <div class="dialog_content1">
          <h3>您的基础版账号<span>项目管理数量已达上限</span>,如需增加请购买<span>项目管理扩容包</span></h3>
          <h2>付费套餐</h2>
        </div>
        <div class="dialog_content_info">
          <div class="clear">
            <div class="dialog_content_item left" v-for="(item,index) of confirmData.list" :key='index'>
              <span></span>
              <span>{{item.name}}</span>
              <span>{{item.price}}</span>
            </div>
          </div>
        </div>
        <div class="dialog_content_bottom">{{confirmData.tel}}</div>
        <div class="dialog_content_bottom1">具体详情请联系我们进行咨询</div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import {
  downloadaddProjectExcel,
  addProjectExcel,
  projectList,
  tipNum,
  deleteProjectAddFund,
  fileTemplate,
  downloadProjectExcel
} from '../../config/api'
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
export default {
  data() {
    return {
      //   本页面数据
      // naveList:[{name:'新项目'},{name:'立项申请'},{name:'预审会'},{name:'投决会'},{name:'签约/付款'},{name:'投后管理'},{name:'储备项目库'}],  //导航栏数据
      naveList: [],
      datalist: [], //项目列表
      addaccountDialogVisible1: false, //付费弹窗
      confirmData: {
        // general: {},
        list: [],
      },
      floor: 0,
      current: 0,
      keyWord:'',
      total:0,
      isOrderByPeople: false,
    }
  },
  created() {
  	if (this.$route.query.floor) {
      this.floor = parseInt(this.$route.query.floor);
    }
    if (this.$route.query.current) {
      this.current = parseInt(this.$route.query.current);
    }
    
    if (this.$route.query.id != undefined) {
      this.$router.push({
        query: {
          id: this.$route.query.id,
          cName: this.$route.query.cName
        },
        name: 'newProjectProcess'
      });
    } else {
      this.getProjecStatus(1);
    }
  },
  computed: {},
  mounted() {},
  methods: {
    //------------本页面方法
    cutNav(index, floor) {
      this.current = index;//设置当前nav的选中样式
      this.floor = floor;//当前的状态
      if (this.isOrderByPeople) {
      	var is_order_people = 1;
      } else{
      	var is_order_people = 0;
      }
      projectList({
        floor: floor,
        search:this.keyWord,
        is_order_people: is_order_people
      }).then(res => {
        if (res.data.msgNo == 1) {
          this.datalist = res.data.payload;
          
          this.total = res.data.payload.length;
        }
      })
    },
    // 去下一个页面
    toNewProjectProcess(id, name) {
      this.$router.push({
        query: {
          id: id,
          cName: name,
          floor: this.floor,
          current:this.current,
        },
        name: 'newProjectProcess'
      });
    },
    AddPriject() {
      this.$router.push({
        name: 'newProjectProcess',
      });
    },
    //获取项目状态列表
    getProjecStatus(type) {
      tipNum().then(res => {
        if (res.data.msgNo == 1) {
          this.naveList = res.data.payload;
          if(type==1){
            this.cutNav(this.current,this.floor);
          }
        }
      })
    },
    //导入项目信息文件
    uploadProjectExcel(e) {
      let inputDOM = this.$refs.inputer;
      // 通过DOM取文件数据
      let file = inputDOM.files[0];
      if (file == undefined) {
        return false;
      }
      if (!/\.xl(s[xmb]|t[xm]|am|s)$/.test(file.name)) {
        dialogConfirm('info', '请选择Excel格式文件')
        inputDOM.value = '';
        return false
      }
      addProjectExcel({}, file).then(res => {
        if (res.data.msgNo == 1) {
          dialogConfirm('success', '导入成功');
          inputDOM.value = '';
          this.cutNav(0, 1);
        } else if (res.data.msgNo == 9527) {
          inputDOM.value = '';
          this.confirmData = res.data.payload[0];
          this.addaccountDialogVisible1 = true;
        } else {
          inputDOM.value = '';
          dialogConfirm('info', res.data.message);
        }
      })
    },
    //下载模板
    toDownloadCusExcel() {
      fileTemplate({
        fileName:'ytxmqkb'
      }).then(res => {
        if (res.data.msgNo == 1) {
          window.location.href = res.data.payload;
        } else {
          dialogConfirm('info', res.data.message)
        }
      });
    },
    //下载项目信息excel
    downloadProjectInfo(){
		if (this.isOrderByPeople) {
	      	var is_order_people = 1;
	      } else{
	      	var is_order_people = 0;
	      }
    		downloadProjectExcel({
    			floor: this.floor,
	        search:this.keyWord,
	        is_order_people: is_order_people
    		}).then(res=>{
    			if (res.data.msgNo == 1) {
	          window.location.href = res.data.payload;
	        } else {
	          dialogConfirm('info', res.data.message)
	        }
    		})
    },
    toCancleConfirm1() {
      this.addaccountDialogVisible1 = false;
    },
    goToDelete(id, type) {

      dialogConfirmHasbtn('确定要删除该项目？').then(() => {
        deleteProjectAddFund({
          type: type,
          id: id,
        }).then(res => {
          if (res.data.msgNo == 1) {
            dialogConfirm('success', '删除成功');
            this.cutNav(this.current, this.floor);
            this.getProjecStatus()
          } else {
            dialogConfirm('info', res.data.message)
          }
        });
      }).catch(() => {});
    },
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/modal.scss';
.table-group-title-btn {
  &>span {
    display: inline-block;
    width: 20px;
    height: 20px;
  }
  .icon_upload {
    background: url('../../images/icon_upload.png') no-repeat;
    background-size: 90%;
  }
  .icon_download {
    background: url('../../images/icon_download.png') no-repeat;
    background-size: 100%;
    margin-top: 4px;
  }
}

.peopleSelectTitle-text {
  font-size: 18px;
  color: #3C98E8;
  font-weight: bold;
  line-height: 40px;
  margin-right: 10px;
}

.peopleSelectTitle {
  border-bottom: 1px solid #dcdfe6;
}

.table-span {
  background: #E7F4FF;
  color: #3C98E8;
  font-size: 12px;
  margin-right: 5px;
  margin-bottom: 5px;
  padding: 3px 5px;
}

.dialog-btn-submit {
  width: 94px;
  height: 36px;
  font-size: 14px;
  float: right;
  margin-left: 20px;
}

.navbox {
  display: flex;
  flex-wrap: nowrap; // width: calc(100% - 14px);
  width: 100%;
  .navItem {
    height: 44px;
    line-height: 40px;
    border: 1px solid #EAEAEA;
    flex: 1;
    border-left: none;
    text-align: center;
    position: relative;
    padding-left: 14px;
    cursor: pointer;
    &>span {
      display: inline-block;
      vertical-align: middle;
    }
    ;
    .icon {
      width: 20px;
      height: 20px;
      line-height: 18px;
      text-align: center;
      border: 1px solid #A0A4AA;
      border-radius: 50%;
      color: #A0A4AA;
    }
    .name {
      color: #A0A4AA;
      font-size: 14px;
    }
    .number {
      display: inline-block;
      width: 16px;
      height: 16px;
      line-height: 16px;
      background-color: #F01400;
      color: white;
      font-size: 12px;
      border-radius: 50%;
      position: absolute;
      top: 5px;
      z-index: 101
    }
    .triangle {
      z-index: 100;
      position: absolute;
      right: -14px;
      width: 0px;
      height: 0px;
      top: 0px;
      display: block;
      border-top: 21px solid transparent;
      border-left: 14px solid #fff;
      border-bottom: 21px solid transparent;
    }
  }
  .current {
    background: #3C98E8;
    .icon {
      border: 1px solid #ffffff;
      color: #ffffff;
    }
    .name {
      color: #ffffff;
    }
    .triangle {
      border-left: 14px solid #3C98E8;
    }
  }
  .navItem::after {
    content: '';
    z-index: 99;
    position: absolute;
    right: -16px;
    width: 0px;
    top: 0px;
    bottom: 0px;
    height: 0px;
    display: block;
    border-top: 21px solid transparent;
    border-left: 15px solid #EAEAEA;
    border-bottom: 21px solid transparent;
  }
  .navItem:first-child {
    border-left: 1px solid #EAEAEA;
  }
  .navItem:last-child {
    .icon {
      //  background: url('../../images/icon_cbxm.png') no-repeat center;
      //  background-size: 100%;
      //  border: none;
    }
    .triangle {
      display: none;
    }
  }
  .navItem:last-child::after {
    display: none;
  }
}

.dialog_content1 {
  h3 {
    text-align: center;
    margin-top: 70px;
    font-size: 16px;
    span {
      color: #3C98E8;
    }
  }
  h2 {
    text-align: center;
    color: #4A90E2;
    font-size: 16px;
    margin-top: 18px;
    font-weight: bold;
  }
}

.dialog_content_info {
  text-align: center;
  &>div {
    width: 465px;
    height: 85px; // background-color: red;
    padding-top: 10px;
    display: inline-block;
    position: relative;
    left: 3px;
    .dialog_content_item {
      width: 50%;
      padding: 0 20px;
      margin-bottom: 10px;
      display: flex;
      align-items: center;
      &>span:nth-child(1) {
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background-color: #3C98E8;
      }
      &>span:nth-child(2) {
        font-size: 14px;
        color: #3C98E8;
        width: calc(100% - 66px);
        text-align: left;
        padding-left: 8px;
      }
      &>span:nth-child(3) {
        width: 60px;
        height: 24px;
        line-height: 24px;
        background: url('../../images/icon_back.png') no-repeat center;
        background-size: 100%;
        background-position-y: -2px;
        font-size: 12px;
        color: white;
      }
    }
  }
}

.dialog_content_bottom {
  font-size: 20px;
  color: #fff;
  position: absolute;
  bottom: 47px;
  width: calc(100% - 17px);
  height: 20px;
  text-align: center;
}

.dialog_content_bottom1 {
  font-size: 12px;
  color: #A0A4AA;
  position: absolute;
  bottom: 93px;
  width: calc(100% - 50px);
  height: 20px;
  text-align: center;
}
.table-content-title-right>div:nth-child(2){
	margin-left: 40px;
}
</style>



